.team {
  background-color: #212833;

  &__wrapper {
    max-height: 1800px;
    padding: 100px 0;
    background-color: var(--light-color);
    border-radius: 10%;
    position: relative;
  }

  &__title-back{
    width: 2525px;
    height: 248px;
    font-size: 200px;
    line-height: 248px;
    text-align: center;
    text-overflow: clip;
    position: relative;
    top: 70px;
  }



  &__title {
    padding-bottom: 30px;
    font-weight: 700;
    font-size: 40px;
    line-height: 49.6px;
    color: #000;
    text-align: center;
  }

  &__inner-title {
    margin-bottom: 10px;
    display: inline-block;
    padding: 14px 40px;
    border-radius: 20px;
    background-color: #b40d0d;
    color: #fff;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }

  &__inner-container {
  }

  &__list {
    height: 330px;
    padding-bottom: 25px 0 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    border: 1px solid #d9d9d9;
    border-radius: 20px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 10px;
    list-style-type: none;
  }

  &__item-name {
    font-weight: 700;
    font-size: 16px;
    line-height: 19.84px;
    color: #000;
    text-align: center;
  }

  &__item-inner {
    display: flex;
    justify-content: space-between;
  }

  &__item-left{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
  }

  &__item-city {
    font-weight: 400;
    font-size: 16px;
    line-height: 19.84px;
  }

  &__item-tel {
    font-weight: 400;
    font-size: 10px;
    line-height: 17.36px;
    color: #B9B9B9;
  }

  &-btn{
    text-align: center;
    padding: 14px 50px;
    max-width: 205px;
    height: 45px;
  }

  &-img {
    display: block;
  }

  &__img-container {
    position: relative;
    bottom: 1700px;
    left: 300px;
  }


  &-flex{
    height: 100%;
    flex-wrap: wrap;
  }
}

.border-list{
  border: 1px solid #d9d9d9;
  border-radius: 20px;
  background-color: #fff;
}

.team-height{
  height: 330px;
}

.main-team {
  max-width: 483px;
}



.team-width {
  display: block;
}

.content-card-width{
  width: 250px;
}

.content {
  padding-bottom: 30px;
  max-width: 1340px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 50px;
  align-items: flex-start;
}

.swiper {
  width: 100%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  gap: 20px;
}

.swiper-slide {
  grid-column: 3 span;
  min-width: 300px;
}


.team-grid{
  grid-column: 1 span;
}



@media (max-width: 1370px){
  .team__title-back{
    display: none;
  }

}

@media (max-width: 1280px){

  .team__wrapper{
    max-height: 1500px;
  }
  .team__img-container{
    bottom: 1400px;
  }
}

@media (max-width: 800px){

  .team__wrapper{
    border-radius: 0px;
  }

  .team__list{
    flex-wrap: wrap;
  }
  .team__list-width img{
    width: 120px;
    height: 120px;
  }

}

@media (max-width: 494px) {
  .main-team img{
    width: 130px;
    height: 130px;
  }
}


@media (max-width: 442px) {

  .team__wrapper{
    max-height: 1700px;
  }

  .team__list-width{
    height: 500px;
  }

  .team__img-container{
    bottom: 50px;
    left: 5px;
  }
}

@media (max-width: 350px) {

  .team{
    max-width: 320px;
  }
}








