.social {
  padding-bottom: 100px;
  background-color: #212833;

  &__telegram {
    position: relative;
    bottom: 200px;
  }

  &__telegram-top {
    padding-bottom: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    background-color: var(--light-color);
    border-radius: 20px;
    position: relative;
    top: 200px;
  }

  &__telegram-title {
    padding-top: 50px;
    font-weight: 700;
    font-size: 36px;
    line-height: 44.64px;
    text-align: center;
    text-transform: uppercase;
    color: #000;
  }

  &__telegram-back {
    width: 2525px;
    height: 248px;
    font-size: 200px;
    line-height: 248px;
    text-align: center;
    text-overflow: clip;
    position: absolute;
    top: 100px;
    left: -620px;
  }

  &__telegram-text {
    max-width: 1005px;
    height: 50px;
    font-weight: 400;
    font-size: 20px;
    line-height: 24.8px;
    text-align: center;
    color: #797979;
  }

  &__telegram-btn {
    padding: 21px 93px;
  }

  &__youtube-container {
    margin-bottom: 30px;
    margin: 0 auto;
  }

  &__youtube-title {
    margin: 0px auto 30px;
    max-width: 989px;
    height: 100px;
    font-weight: 700;
    font-size: 40px;
    line-height: 49.6px;
    text-align: center;
    text-transform: uppercase;
    color: var(--light-color);
  }

  &__youtube-btn {
    padding: 21px 98px;
  }
}

@media (max-width: 1370px) {
  .social__youtube-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
  }

  .video-width {
    width: 700px;
    height: 590px;
  }
}

@media (max-width: 1000px) {
  .social__telegram-back{
    display: none;
  }
}

@media (max-width: 750px) {
  .social__telegram {
    bottom: 50px;
  }

  .social__telegram-top {
    top: 50px;
  }

  .social__telegram-text {
    margin-bottom: 50px;
  }

  .social__youtube-title {
    font-size: 22px;
    line-height: 25.32px;
  }
}

@media (max-width: 600px) {
  .social__telegram-title {
    max-width: 320px;
    font-size: 16px;
    line-height: 19.84px;
  }

  .social__telegram-text {
    padding-bottom: 0;
    margin-bottom: 0;
    font-size: 12px;
    line-height: 14.88px;
    margin-bottom: 30px;
  }

  .social__telegram-btn {
    width: 270px;
    height: 55px;
    font-size: 16px;
    line-height: 20.8px;
    padding: 20px 35px 18px 35px;
    text-align: center;
  }

  .video-width {
    width: 320px;
    height: 240px;
  }

  .social__youtube-btn {
    width: 300px;
    height: 55px;
    font-size: 16px;
    line-height: 20.8px;
    padding: 20px 48px 18px 35px;
    text-align: center;
  }
}

@media (max-width: 350px) {
  .social {
    max-width: 320px;
  }

  .social__telegram-btn {
    padding-top: 7px;
    width: 260px;
  }

  .social__youtube-btn {
    width: 260px;
    padding-left: 22px;
  }
}
