.reviews {
  padding: 100px 0;
  background-color: #212833;

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }

  &__inner-top {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__inner-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }

  &__title {
    font-weight: 700;
    font-size: 40px;
    line-height: 49.6px;
    text-transform: uppercase;
    color: #fff;
  }

  &__btn {
    padding: 14px 59px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20.8px;
    text-align: center;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 10px;
  }

  &__inner-text {
    max-width: 466px;
    font-weight: 400;
    font-size: 20px;
    line-height: 24.8px;
    color: #fff;
  }

  &__buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
  }

  &__inner-bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }

  &__block {
    padding: 22px 22px 30px 20px;
    background-color: #fff;
    border-radius: 20px;
  }

  &__block-title {
    padding-bottom: 15px;
    font-weight: 700;
    font-size: 18px;
    line-height: 22.32px;
    color: #000;
  }

  &__block-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 17.36px;
  }

  &__slider {
    padding: 50px 20px;
  }

  &__slider-title {
    padding-bottom: 10px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29.76px;
  }

  &__slider-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 22.32px;
  }
}


.rev-next,
.rev-prev {
  cursor: pointer;
}

.title-none {
  display: none;
}

@media (max-width: 1200px) {
  .reviews__container {
    flex-direction: column;
  }
  .reviews__title {
    display: none;
  }

  .title-none {
    display: block;
  }

  .reviews__inner-bottom {
    display: none;
  }

  .rev-next,
  .rev-prev {
    display: none;
  }
}

@media (max-width: 700px) {
  .reviews__slider {
    padding: 10px;
  }

  .reviews__slider-title {
    font-size: 14px;
    line-height: 17.36px;
  }

  .reviews__slider-text {
    width: 253px;
    height: 420px;
    font-size: 12px;
    line-height: 14.88px;
  }

  .rev-width{
    width: 270px;
    height: 473px;
  }

}

@media (max-width: 350px) {
  .reviews{
    max-width: 320px;
  }

}
