.process {
  background-color: #212833;

  &__flex {
    display: flex;
    justify-content: flex-end;
    max-height: 900px;
  }

  &__wrapper {
    padding: 100px;
    max-width: 1207px;
    max-width: 953px;
    border-top-left-radius: 10%;
    border-bottom-left-radius: 10%;
    background-color: #fff;
    position: relative;
    top: 200px;
  }

  &__title-back {
    width: 754px;
    height: 296px;
    padding: 0;
    font-size: 238.68px;
    line-height: 295.96px;
    text-align: center;
    align-self: center;
    position: relative;
    top: 100px;
    left: 70px;
  }

  &__title {
    margin-bottom: 39px;
    max-width: 710px;
    height: 200px;
    font-size: 40px;
    font-weight: 700;
    line-height: 49.6px;
    text-transform: uppercase;
    color: #000;

    span {
      color: #b81e1e;
    }
  }

  &__list {
    padding-bottom: 30px;
    max-width: 734px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }

  &__numbers {
    width: 84px;
    height: 84px;
    padding: 19px 16px;
    background-color: #000;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__item-numbers {
    width: 52px;
    height: 45px;
    font-weight: 700;
    font-size: 36px;
    line-height: 44.64px;
    -webkit-text-stroke: 1px var(--light-color);
    text-stroke: 1px var(--light-color);
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__item-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 29.76px;
    color: #000;
  }

  &__item-text {
    font-weight: 300;
    font-size: 16px;
    line-height: 19.84px;
    color: #000;
  }

  &__bottom {
    padding: 22px 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 734px;
    height: 123px;
    background-color: #000;
    border-radius: 20px;
  }

  &__bottom-text {
    font-weight: 500;
    font-size: 20px;
    line-height: 24.8px;
    color: var(--light-color);
  }

  &__bottom-time {
    font-family: var(--second-family);
    font-size: 20px;
    font-weight: 500;
    line-height: 24.2px;
    color: #6c6c6c;
  }

  &__img {
    display: block;
    width: 100%;
  }
}

.numbers-width {
  width: 64px;
}

@media (max-width: 1550px) {
  .process__title-back{
    display: none;
  }

}


@media (max-width: 1000px) {
  .process__title {
    font-size: 30px;
    line-height: 39.32px;
    margin-bottom: 0;
  }


}

@media (max-width: 733px) {
  .process__wrapper {
    padding: 50px;

  }

  .process__title {
    font-size: 24px;
    line-height: 30.32px;
    margin-bottom: 0;
    height: 100px;
    margin-bottom: 30px;
  }

  .process__numbers {
    width: 70px;
    height: 55px;
    padding: 15px;
  }

  .process__item-numbers {
    width: 45px;
    height: 30px;
    font-size: 26px;
    line-height: 30.88px;
  }

  .process__item-title {
    font-family: Unbounded;
    font-size: 15px;
    line-height: 14.88px;
  }

  .process__item-text {
    font-size: 12px;
    line-height: 14.88px;
  }

  .process__bottom {
    gap: 7px;
  }

  .process__bottom-text {
    font-size: 12px;
    line-height: 14.88px;
  }

  .process__bottom-time {
    font-size: 12px;
    line-height: 14.52px;
  }
}

@media (max-width: 500px) {
  .process__wrapper {
    padding: 20px;
    top: 80px;
  }

  .process__title {
    font-size: 18px;
    line-height: 22.32px;
  }
}

@media (max-width: 350px) {
  .process {
    max-width: 320px;
  }
}
